*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Inter', sans-serif;
}

html,body{
  height: 100%;
  background-color: #0F0A0A;
  scroll-behavior: smooth;
}

.container{
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 2%;
}

.main-button{
  padding: 8px 60px;
  border: 0;
  border-radius: 20px;
  cursor: pointer;
}

.bg-black{
  background-color: black;
  z-index: 900;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  transition: 0.6s;
}

.flex{
  display: flex;
}

.title-section{
  color: #F4FAFF;
  text-align: center;
  font-family: 'Ubuntu', sans-serif;
  font-weight: bold;
  font-size: 36px;
  margin: 80px 0;
}

.button{
  position: relative;
  padding: 10px 25px;
  font-size: clamp(0.1em, 0.1em + 3vw, 1.1em);
  font-weight: 500;
  letter-spacing: 1px;
  border-radius: 5px;
  border: 1px solid transparent;
  outline: none;
  cursor: pointer;
  background: #0d0d0d;
  overflow: hidden;
  transition: 0.6s;
  color: #0183CF;
  border-color: #0183CF;
  z-index: 995;
}


.button:before, .button:after{
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  height: 100%;
  filter: blur(30px);
  opacity: 0.4;
  transition: 0.6s;
}


.button:before{
  width: 60px;
  background: rgba(255,255,255,0.6);
  transform: translateX(-130px) skewX(-45deg);
}


.button:after{
  width: 30px;
  background: rgba(255,255,255,0.6);
  transform: translateX(-130px) skewX(-45deg);
}

.button:hover:before,
.button:hover:after{
  opacity: 0.6;
  transform: translateX(320px) skewX(-45deg);
}


button:hover{
  color: #f2f2f2;
}


.button:hover{
  background: #0183CF;
}

.display-none{
  display: none;
}

::-webkit-scrollbar-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.9);
	border-radius: 10px;
	background-color: #222;
}

::-webkit-scrollbar{
	width: 8px;
	background-color: #000;
}

::-webkit-scrollbar-thumb{
	border-radius: 10px;
	background-color: #0183CF;
}

.hidden{
  transition: 1s;
  filter: blur(5px);
  transform: translateX(-100%);
  opacity: 0;
}

.show{
  opacity: 1;
  filter: blur(0);
  transform: translateX(0);
}

.hidden-port{
  filter: default;
  transform: default;
}


.delay-1{
  transition-delay: 200ms !important;
}

.delay-2{
  transition-delay: 400ms !important;
}

.delay-3{
  transition-delay: 600ms !important;
}





/*HEADER*/

.main-bg{
  background-image: url("./assets/images/background-1.jpg");
  background-position: top;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  position: relative;
}

header{
  display: flex;
  justify-content: space-between;
  padding: 40px 5%;
  position: relative;
  width: 100%;
  z-index: 999;
  transition: 0.6s;
}

.sticky{
  background-color: rgba(0,0,0,0.7);
  position: fixed;
  z-index: 999;
  padding: 30px 5%;
}

header .logo{
  padding: 0 5%;
  color: #F4FAFF;
  position: relative;
}

header .logo img{
  position: absolute;
  top: -16px;
  left: 60%;
}

header .menu-desktop{
  padding:  0 5%;
}

header .menu-desktop ul{
  list-style: none;
}

header .menu-desktop li{
  display: inline-block;
  padding: 0 20px;
}

header .menu-desktop a{
  text-decoration: none;
  position: relative;
  font-size: 19px;
  font-family: 'Ubuntu', sans-serif;
  font-weight: bold;
  color: #F4FAFF;
  transition: color .3s cubic-bezier(0.11, 0.7, 0, 1);
}

header a:hover{
  color: #0183CF;
}

header a:after {
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #0183CF;
  content: "";
  transform: scale(0);
  transition: transform .3s cubic-bezier(0.11, 0.7, 0, 1);
}

header a:hover:after {
  transform: scale(1);
}


header .menu-mobile{
  display: none;
}

header .menu-mobile ul{
  position: absolute;
  left: 0;
  top: 81.4px;
  text-align: left;
  width: 100%;
  background-color: rgba(0,0,0,0.7);
  padding: 30px 0;
  z-index: 999;
  animation-name: aniMenu;
  animation-duration: 0.6s;
}

@keyframes aniMenu{
  from {top:10%; opacity:0}
  to {top:81.4px; opacity:1}
}

.menu-mobile li{
  list-style: none;
  padding: 15px 3%;
}

.menu-mobile a{
  text-decoration: none;
  font-size: 20px;
  color: #F4FAFF;
  font-weight: bold;
}

.menu-btn {
  height: 22px;
  width: 40px;
  cursor: pointer;
}

.menu-btn span,
.menu-btn span::before,
.menu-btn span::after {
  background: #F4FAFF;
  border-radius: 3px;
  content: '';
  position: absolute;
  width: 30px;
  height: 4px;         
  margin-top: 13px; 
  -webkit-transition: .3s ease-in-out;
  -moz-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}

.menu-btn span::before {
  margin-top:-12px;
}

.menu-btn span::after {
  margin-top:12px;
  }

.menu-btn.active span {
  background: transparent;
}

.menu-btn.active span::before {
  margin-top: 0;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.menu-btn.active span::after {
  margin-top: 0;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

/****/

/**MAIN-CALL**/

.main-call{
  padding: 200px 0;
  height: 100vh;
  position: relative;
  z-index: 900;
}

.main-call .flex{
  justify-content: space-between;
}

.main-call .left-side{
  text-align: center;
}

.main-call .left-side h2{
  font-family: 'Ubuntu', sans-serif;
  font-weight: bold;
  color: white;
  font-size: clamp(1.75em, 1em + 3vw, 2.15em);
  margin-bottom: 15px;
}

.main-call .left-side p{
  margin-top: 15px;
}

.main-call .left-side strong{
  color: #0183CF;
}

.right-side{
  text-align: right;
}

.right-side .myself{
  width: 100%;
}

.right-side img{
  width: 80%;
  border-radius: 15% 85% 7% 93% / 84% 11% 89% 16%;
  -webkit-box-shadow: 10px 10px 18px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 10px 10px 18px 0px rgba(0,0,0,0.75);
  box-shadow: 10px 10px 18px 0px rgba(0,0,0,0.75);
  position: relative;
  top: -50px;
  transition: 0.6s;
}

.right-side img:hover{
  scale: 1.1;
}


.main-call .digitar{
  font-size:clamp(0.2em, 1em + 3vw, 1em);
  color: #24A8F3;
  text-align: center;
  width: 0;
	white-space: nowrap;
	overflow: hidden;
	border-right: 4px solid #212121;
	animation: cursor 1s step-start infinite, 
  text 2s steps(18) alternate;
  animation-fill-mode: forwards;
  margin-bottom: 10px;
  position: relative;
  left: 50%;
  transform: translate(-50%);
}

.main-call .button{
  margin-top: 10px;
}

@keyframes cursor {
	0%, 100% { 
    border-color: #212121; 
  }
}

@keyframes text {
	0% { 
    width: 0; 
  }

  99%{
    width: 60%;
  }

	100% { 
    width: fit-content; 
  }
}


/**SOBRE**/

.return-top{
  position: fixed;
  bottom: 50px;
  right: 50px;
  background-color: rgba(1, 131, 207, 0.5);
  z-index: 9999;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  text-align: center;
  font-size: 35px;
  cursor: pointer;
  opacity: 0;
  transition: 0.6s;
  scale: 0;
}

.return-top svg{
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
}

.return-top:hover svg{
  animation-name: returnUp;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  color: rgba(255,255,255,1);
}

.return-top:hover{
  background-color: rgba(1, 131, 207, 0.8);
}

@keyframes returnUp{
  25%{
    top: 47%;
  }

  40%{
    top: 55%;
  }

  100%{
    top: 35%;
  }
}

.return-show{
  color: rgba(255,255,255,0.6);
  opacity: 1;
  scale: 1;
}

.sobre{
  padding: 0 0 160px 0;
  background-color: #000;
}

.sobre .loot{
  width: 40%;
  background-color: #ccc;
}

.sobre .info{
  width: 60%;
  text-align: justify;
  padding: 0 5%;
}

.info h1{
  color: #24A8F3;
  font-family: 'Ubuntu', sans-serif;
  font-style: italic;
  margin-bottom: 18px;
  font-size: 27px;
}

.info p{
  color: #F4FAFF;
  font-size: 16px;
  text-align: left;
}

/****/

/**SKILLS**/

.skills{
  padding: 0 0 100px 0;
}

.list-skills{
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}

.item-skill{
  width: 33.333%;
  padding: 40px 50px;
  position: relative;
}

.item-wrapper{
  border-bottom: 2px solid #0183CF;
  padding: 10px;
  background-color: #212121;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  cursor: pointer;
  overflow: hidden;
  max-height: 100px;
  transition: 2s ease;
  -webkit-box-shadow: 9px 12px 12px -1px rgba(0,0,0,0.75);
  -moz-box-shadow: 9px 12px 12px -1px rgba(0,0,0,0.75);
  box-shadow: 9px 12px 12px -1px rgba(0,0,0,0.75);
}

.item-logo{
  position: absolute;
  top: 0px;
  left: 50%;
  transform: translate(-50%);
  background-image: linear-gradient(60deg, #2AAA8A, #4169E1);
  border-radius: 100%;
  padding: 15px;
}

.bg-wrap{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #212121;
  width: 90%;
  height: 90%;
  border-radius: 100%;
  z-index: 1;
}


.item-wrapper:hover{
  max-height: 600px;
}

.item-skill h2{
  color: #24A8F3;
  margin-top: 50px;
  font-size: 22px;
  font-family: 'Ubuntu', sans-serif;
  font-weight: bold;
  text-align: center;
}

.item-skill p{
  margin-top: 20px;
  color: #F4FAFF;
  text-align: center;
  padding: 0 2% 2% 2%;
  font-weight: 300;
  font-size: 16px;
}

.skill-api{
  justify-content: space-between;
  padding: 0 5%;
  margin-top: 50px;
}

.api-left{
  width: 40%;
}

.api-left .item-skill{
  width: 100%;
}

.api-right{
  position: relative;
  width: 60%;
  text-align: center;
}

.api-right h3{
  font-size: 900;
  color: #F4FAFF;
  margin-bottom: 10px;
}

.pokedex{
  width: 50%;
}

.pokemon{
  position: absolute;
  left: 48%;
  top: 43%;
  transform: translate(-50%, -50%);
  width: 10%;
}

.poke-data{
  position: absolute;
  left: 51%;
  top: 59.4%;
  transform: translate(-50%, -50%);
  font-family: 'Ubuntu', sans-serif;
  font-style: italic;
  font-size: clamp(2px, 5vw, 19px);
}

.poke-number{
  color: #aaa;
}

.poke-name{
  color: #3a444d;
  text-transform: capitalize;
}

.poke-form{
  position: absolute;
  top: 70%;
  left: 48%;
  transform: translate(-50%, -50%);
  background-color: green;
  width: 25%;
}

.poke-search{
  width: 100%;
  padding: 4%;
  outline: 0;
  font-weight: 600;
  color: #3a444d;
  font-size: clamp(2px, 5vw, 12px);
  box-shadow: -2px 1px 0 #888, -3px 3px 0 #333;
}

.poke-button{
  position: absolute;
  left: 48%;
  bottom: 5%;
  transform: translate(-50%, -50%);
  width: 25%;
  justify-content: space-between;
}

.poke-button .button{
  width: 40%;
  padding: 4%;
  border: 2px solid #000;
  border-radius: 3px;
  font-size: clamp(2px, 5vw, 12px);
  font-weight: 600;
  color: white;
  background-color: #444;
  box-shadow: -2px 1px 0 #222, -3px 3px 0 #000;
  cursor: pointer;
  white-space: nowrap;
}

.poke-button .button:active{
  box-shadow: inset -4px 4px 0 #222;
  font-size: 0.7rem;
}
/****/

/**PORTIFOLIO**/

.portifolio{
  padding: 0 0 40px 0;
  background-color: #000;
}

.port-box{
  width: 100%;
  flex-wrap: wrap;
  text-align: center;
}

.port-info{
  width: 33.333%;
  padding: 50px;
  cursor: pointer;
}

.port-info-wrapper{
  transition: 0.6s ease;
}

.port-info-wrapper:hover{
  transform: scale(1.2);
}

.port-info-wrapper:hover .bg-black{
  opacity: 0;
}


.port-title{
  background-color: #212121;
  color: #F4FAFF;
  padding: 8px 0;
}

.port-title h3{
  font-style: italic;
  font-size: 19px;
}

.port-img{
  position: relative;
}

.port-img p{
  color: #F4FAFF;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 994;
  font-size: 25px;
}

.port-skills{
  background-color: #212121;
  color: #F4FAFF;
  padding: 10px 0;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.modal-bg-black{
  position: fixed;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0.6;
  width: 100%;
  height: 100%;
  z-index: 999;
}

.modal{
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 30px 80px 100px 80px;
  background-color: #212121;
  z-index: 9999;
  width: 72vw;
  border-radius: 10px;
  cursor: default;
  transition: 0.6s;
  animation-name: modalIn;
  animation-duration: 0.6s;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
}

@keyframes modalIn {
  from {top:10%; opacity:0}
  to {top:50%; opacity:1}
}

.modal a{
  color: inherit;
  text-decoration: none;
}

.modal-left{
  width: 50%;
}

.modal-left .modal-video{
  width: 100%;
  background-color: #ccc;
  height: 100%;
  margin-bottom: 30px;
}

.modal-video video{
  width: 100%;
  height: 100%;
  object-fit: fill;
}


.modal-right{
  width: 50%;
  padding: 0 0 0 3%;
}

.modal-right h3{
  color: #24A8F3;
  margin-bottom: 30px;
  font-size: 27px;
  text-align: left;
  font-weight: normal;
  font-style: italic;
}

.modal-right p{
  color: #aaa;
  font-size: 14px;
  text-align: left;
}

.modal-right span{
  display: block;
  padding-top: 50px;
  font-size: 32px;
  color: #0183CF;
}

.modal-right button{
  display: none;
}

.modal-close{
  position: absolute;
  right: 15px;
  top: 15px;
  transition: 0.6s;
}

.modal-close button{
  color: #24A8F3;
  font-weight: bold;
  cursor: pointer;
  background-color: #000;
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 0;
}


.modal-close:hover{
  color: #0183CF;
  transform: scale(1.2);
}

.no-modal{
  display: none;
}

/****/

/**CONTATO**/

.contato{
  width: 100%;
  text-align: center;
}

.contato .flex{
  justify-content: space-between;
  padding: 10px 0 180px 0;
}

.contato-type{
  transition: 0.6s ease;
  cursor: pointer;
}

.contato-type:hover .contato-icon{
  scale: 1.2;
}

.contato-type a{
  text-decoration: none;
}

.contato-icon{
  display: inline-block;
  background-color: #000;
  color: #0183CF;
  font-size: 32px;
  padding: 24px 23px 20px 23px;
  border-radius: 50%;
  transition: 0.6s ease;
}

.contato-type{
  width: 33.33333%;
}

.contato-type h4{
  color: #F4FAFF;
  margin-top: 15px;
  font-size: 19px;
}

.contato-type p{
  color: #ccc;
  margin-top: 5px;
  font-size: 16px;
}

/****/

/**FOOTER**/

footer{
  text-align: center;
  padding: 40px 0;
  color: #F4FAFF;
  background-color: #000;
  font-size: 14px;
}

/****/

@media screen and (max-width: 1000px) {

  /**HEADER**/

  header{
    background-color: rgba(0,0,0,0.7);
    position: fixed;
    z-index: 999;
  }

  header .menu-desktop{
    display: none;
  }

  header .menu-mobile{
    display: block;
  }

  header .logo img{
    position: absolute;
    top: -16px;
    left: 10%;
  }

  /****/

  /**MAIN-CALL**/

  .main-call{
    min-height: 90vh;
  }

  .main-call .flex{
    flex-direction: column-reverse;
    text-align: center;
    flex-wrap: wrap;
  }

  .main-call .left-side{
    border-top: 10px;
    margin-top: 10px;
  }


  .main-call .right-side{
    text-align: center;
    display: contents;
  }
  
  .right-side img{
    width: 40%;
    top: 0px;
  }

  /****/

  /**SOBRE**/
  
  .sobre .flex{
    flex-direction: column;
    text-align: center;
  }

  .sobre .loot{
    width: 80%;
    margin-bottom: 80px;
    position: relative;
    left: 50%;
    transform: translate(-50%);
  }
  
  .sobre .info{
    width: 100%;
  }

  /****/

  /**SKILLS**/

  .list-skills{
    flex-direction: column;
  }

  .item-skill{
    width: 100%;
    padding: 40px 50px;
    position: relative;
  }

  .skill-api{
    flex-direction: column;
  }

  .api-left{
    width: 100%;
  }

  .api-right{
    width: 100%;
  }

  .pokedex{
    width: 75%;
  }

  .poke-form{
    width: 50%;
  }

  .poke-button{
    width: 50%;
  }

  /****/

  /**PORTIFOLIO**/

  .port-box{
    flex-direction: column;
  }

  .port-info{
    width: 100%;
  }

  .modal-wrapper{
    flex-direction: column;
  }

  .modal{
    padding: 5%;
  }

  .modal-left{
    width: 100%;
  }

  .modal-video{
    margin-top: 30px;
   }

  .modal-left button{
    display: none;
  }
  
  .modal-right{
    width: 100%;
  }

  .modal-right h3{
    margin-top: 50px;
  }

  .modal-right button{
    display: block;
    margin-top: 20px;
    position: relative;
    left: 50%;
    transform: translate(-50%);
  }


  /****/

  /**CONTATO**/

  .contato .flex{
    flex-direction: column;
    justify-content: center;
  }

  .contato-type{
    margin: 50px 0;
    width: 100%;
  }

  /****/
}

@media screen and (max-width: 700px){
  .sobre .loot{
    width: 95%;
  }
}

@media screen and (max-width: 480px) {

  /**HEADER**/

  header .logo img{
    width: 200px;
    height: 50px;
  }

  /****/
  /**MAIN-CALL**/

  .right-side img{
    width: 70%;
    top: 0px;
  }

  /****/

   /**PORTIFOLIO**/

   .modal-video{
    display: none;
   }

   /****/
}